const internalLinkSchema = {
	type: 'object',
	properties: {
		type: { type: 'string' },
		label: { type: 'string' },
		value: {
			type: 'object',
			properties: {
				route: { type: 'string' },
				fields: { type: 'object' }
			}
		},
		opensWindow: { type: 'boolean' },
		download: { type: 'boolean' },
		configAria: {
			type: 'object',
			patternProperties: {
				'^aria-': { type: 'string' }
			},
			additionalProperties: false
		}
	}
};

const externalLinkSchema = {
	type: 'object',
	properties: {
		type: { type: 'string' },
		label: { type: 'string' },
		value: {
			type: 'object',
			properties: {
				href: { type: 'string' }
			}
		},
		opensWindow: { type: 'boolean' },
		download: { type: 'boolean' },
		configAria: {
			type: 'object',
			patternProperties: {
				'^aria-': { type: 'string' }
			},
			additionalProperties: false
		}
	}
};

export const browseLinkSchema = {
	type: 'object',
	properties: {
		type: { type: 'string' },
		icon: { type: 'string' },
		label: { type: 'string' },
		link: { anyOf: [externalLinkSchema, internalLinkSchema] }
	}
};

export const headingSchema = {
	type: 'object',
	properties: {
		type: { type: 'string' },
		hasTile: { type: 'boolean', default: false },
		tile: {
			type: 'object',
			properties: {
				type: { type: 'string', enum: ['icon', 'image', 'block'] },
				content: {
					type: 'object',
					properties: {
						image: {
							type: 'object',
							properties: {
								url: { type: 'string' },
								altText: { type: 'string' },
								alignment: { type: 'string', enum: ['center', 'left'] },
								headerOverImage: { type: 'boolean', default: false }
							}
						},
						icon: {
							type: 'object',
							properties: {
								name: { type: 'string' },
								alignment: { type: 'string', enum: ['center', 'left'] }
							}
						},

						block: {
							type: 'object',
							properties: {
								color: { type: 'string' },
								alignment: { type: 'string', enum: ['center', 'left'] }
							}
						}
					},
					oneOf: [
						{ required: ['image'] },
						{ required: ['icon'] },
						{ required: ['block'] }
					]
				}
			}
		},
		hasIcon: { type: 'boolean' },
		icon: {
			type: 'object',
			properties: {
				name: { type: 'string' },
				placement: { type: 'string' },
				alignment: { type: 'string' }
			}
		},
		label: { type: 'string' },
		link: { anyOf: [externalLinkSchema, internalLinkSchema] }
	}
};

const linksInternalSchema = {
	type: 'array',
	properties: {
		type: { type: 'string' },
		label: { type: 'string' },
		value: {
			type: 'object',
			properties: {
				route: { type: 'string' },
				fields: { type: 'object' }
			}
		},
		opensWindow: { type: 'boolean' },
		download: { type: 'boolean' },
		configAria: {
			type: 'object',
			patternProperties: {
				'^aria-': { type: 'string' }
			},
			additionalProperties: false
		},
		startsWith: {
			type: 'object',
			properties: {
				icon: { type: 'string' },
				align: { type: 'string' }
			}
		},
		endsWith: {
			type: 'object',
			properties: {
				type: { type: 'string' },
				value: { type: 'string' },
				additionalAttrs: {
					type: 'object',
					properties: {
						status: { type: 'string' },
						variant: { type: 'string' }
					},
					additionalProperties: false
				}
			}
		}
	}
};

const linksExternalSchema = {
	type: 'array',
	properties: {
		type: { type: 'string' },
		label: { type: 'string' },
		value: {
			type: 'object',
			properties: {
				href: { type: 'string' }
			}
		},
		opensWindow: { type: 'boolean' },
		download: { type: 'boolean' },
		configAria: {
			type: 'object',
			patternProperties: {
				'^aria-': { type: 'string' }
			},
			additionalProperties: false
		},
		startsWith: {
			type: 'object',
			properties: {
				icon: { type: 'string' },
				align: { type: 'string' }
			}
		},
		endsWith: {
			type: 'object',
			properties: {
				type: { type: 'string' },
				value: { type: 'string' },
				additionalAttrs: {
					type: 'object',
					properties: {
						status: { type: 'string' },
						variant: { type: 'string' }
					},
					additionalProperties: false
				}
			}
		}
	}
};

export const LinksSchema = {
	type: 'array',
	links: {
		anyOf: [linksExternalSchema, linksInternalSchema]
	}
};

export const itemsSchema = {
	type: 'array',
	properties: {
		heading: { schema: headingSchema },
		links: { schema: LinksSchema },
		browseLink: { schema: browseLinkSchema }
	}
};
