import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { isEmpty as _isEmpty } from 'lodash';
import rtlBehavior from '@servicenow/behavior-rtl';
import nowLinkSetGroupStyles from './_now-link-set-group.scss';
import { itemsSchema } from './schemas/linkset-schema';
import '@servicenow/now-heading';
import './now-link-set';

const _renderHeading = (heading) => {
	return !_isEmpty(heading) ? (
		<div className="now-link-set-group-heading">
			<now-heading label={heading} level="1" variant="header-primary" />
		</div>
	) : null;
};
const _renderLinkSetItems = (items, linkSetPerRow, disableBrowseAll, size) => {
	return !_isEmpty(items) ? (
		<div
			className="now-link-set-group-link-set-container"
			style={{ gridTemplateColumns: `repeat(${linkSetPerRow},minmax(0,1fr))` }}>
			{items.map((l) => (
				<now-link-set
					className="-link-set"
					heading={l.heading}
					links={l.links}
					browseLink={disableBrowseAll === true ? {} : l.browseLink}
					size={size}
				/>
			))}
		</div>
	) : null;
};

const nowLinkSetGroup = (state) => {
	const {
		heading,
		items,
		linkSetPerRow,
		size,
		disableBrowseAll = false
	} = state.properties;

	return (
		<article className="now-link-set-group">
			{_renderHeading(heading)}
			{_renderLinkSetItems(items, linkSetPerRow, disableBrowseAll, size)}
		</article>
	);
};

/**
 * A link set group of represents a collection of link set with a text header that provides a short
 * summary of content in the link set.
 *
 * ```jsx
 *
 *  <now-link-set-group disableBrowseAll={false} count="4" heading="Find the info you need" items={linkSetGroup}/>
 *
 * ```
 * @seismicElement now-link-set-group
 * @summary A link set group of represents a collection of link set with a text header that provides a short
 * summary of content in the link set.
 */
createCustomElement('now-link-set-group', {
	properties: {
		/**
		 * Properties to configure the heading for the Link Set Group.
		 *
		 * A text that provides a short summary of content in the link set group.
		 * @type {string}
		 */
		heading: { schema: { type: 'string' } },
		/**
		 * Sets the number of link set per row before it wraps down to next row.
		 * @type {number}
		 */
		linkSetPerRow: {
			default: 5,
			schema: { type: 'number' }
		},
		/**
		 * An array of `Linkset` items.
		 * @type {Array.<Linkset>}
		 */
		items: { default: [], schema: itemsSchema },

		/**
		 * Sets font size for each link set within link set group.
		 * @type {('md'|'lg')}}
		 */
		size: { default: 'lg', schema: { type: 'string', enum: ['md', 'lg'] } },

		/**
		 * Disable the browseAll links, default is false
		 * @type {boolean}
		 */
		disableBrowseAll: { default: false, schema: { type: 'boolean' } }
	},
	renderer: {
		type: snabbdom,
		view: nowLinkSetGroup
	},
	styles: nowLinkSetGroupStyles,
	behaviors: [rtlBehavior]
});
