import { createCustomElement } from '@servicenow/ui-core';
import snabbdom, { Fragment } from '@servicenow/ui-renderer-snabbdom';
import nowLinkSetStyles from './_now-link-set.scss';
import { filterAriaAttributes } from '@servicenow/library-enhanced-utils';
import { isEmpty as _isEmpty, find as _find } from 'lodash';
import { t } from 'sn-translate';
import '@servicenow/now-tooltip';
import '@servicenow/now-icon';
import '@servicenow/now-badge';
import rtlBehavior from '@servicenow/behavior-rtl';

import {
	isHeadingLink,
	isHeadingText,
	containsGroup,
	getGroupLinks,
	isLinkExternal,
	isLinkInternal
} from './utils';

import {
	headingSchema,
	LinksSchema,
	browseLinkSchema
} from './schemas/linkset-schema';

const _getChildElement = (parent, childTag) => {
	if (!parent || parent.childElementCount === 0) return undefined;

	return Array.from(parent.children).find((e) => e && e.tagName === childTag);
};

const _addTooltipEl = (node, content) => {
	if (!node || !content) return;

	if (node.offsetHeight >= node.scrollHeight) return;

	const nowTooltip = document.createElement('now-tooltip');
	nowTooltip.targetRef = node;
	nowTooltip.content = content;

	const toolTip = _getChildElement(node, 'NOW-TOOLTIP');
	if (toolTip) return;

	node.appendChild(nowTooltip);
};

const _removeTooltipEl = (node) => {
	if (!node || !(node instanceof HTMLElement)) return;

	let nowTooltip = _getChildElement(node, 'NOW-TOOLTIP');
	if (!nowTooltip) return;

	node.removeChild(nowTooltip);
};

const Header = ({ heading, variant, size, dispatch }) => {
	const { type } = heading;
	const HeadingAsLink = ({ heading, variant, size }) => {
		const { hasIcon, hasTile = false, tile, icon, link } = heading;
		const { type: tileType } = tile || {};
		const isExternal = isLinkExternal(link.type);
		const isInternal = isLinkInternal(link.type);
		const { placement } = icon;
		const _renderHeaderIcon = (icon, size) => {
			const { name, alignment = 'left' } = icon;
			return (
				<span
					class={{
						'now-link-set-heading-link-icon': true,
						['-' + alignment]: alignment
					}}>
					<now-icon icon={name} size={size} />
				</span>
			);
		};

		const _renderTileImage = (tile) => {
			const { type } = tile || {};
			const {
				url,
				altText = '',
				alignment,
				headerOverImage = false,
				fit = 'cover',
				position = 'center'
			} = tile.content.image || {};
			return (
				<div
					class={{
						'now-link-set-tile-content': true,
						'-has-overlay': headerOverImage,
						'-image': type === 'image',
						['-' + alignment]: alignment
					}}>
					<img
						className="image"
						src={url}
						alt={altText}
						style={{
							objectFit: fit,
							objectPosition: position
						}}
					/>
				</div>
			);
		};

		const _renderTileIcon = (tile) => {
			const { type } = tile || {};
			let { name, alignment } = tile.content[type] || {};
			alignment = 'left'; //hard-coded for unitl tile icon is ready
			return (
				<div
					class={{
						'now-link-set-tile-content': true,
						// '-icon': type === 'icon', add it back when icon is ready
						'-legacy-icon': type === 'icon',
						['-' + alignment]: alignment
					}}>
					<now-icon icon={name} size="xl" />
				</div>
			);
		};

		const _renderTileBlock = ({ color }) => {
			return (
				<div
					class={{
						'now-link-set-tile-content': true,
						'-block': true,
						['-' + color]: color
					}}>
					<div className="now-link-set-tile-content -block-inner" />
				</div>
			);
		};

		const _renderImageOrBlock = (tile) => {
			const { type } = tile;
			return type === 'image'
				? _renderTileImage(tile)
				: _renderTileBlock(tile.content.block);
		};

		const _renderHeaderText = (label) => (
			<span
				hook-insert={({ elm }) => _addTooltipEl(elm, label)}
				hook-destroy={(elm) => _removeTooltipEl(elm)}
				className="now-link-set-heading-link-text">
				{label}
			</span>
		);
		const _renderExternalLink = (link) => {
			const { opensWindow, configAria, download = false } = link;
			const isExternal = isLinkExternal(link.type);

			return (
				<a
					href={`//${link.value.href}`}
					on-click={() =>
						dispatch(() => {
							return {
								type: 'NOW_LINK_SET#LINK_CLICKED',
								payload: {}
							};
						})
					}
					class={{
						'now-link-set-heading-link': true,
						['-one-col-grid']: !hasIcon,
						['-' + placement]: placement,
						['-' + variant]: variant,
						['-' + size]: size
					}}
					target={isExternal || opensWindow ? '_blank' : undefined}
					rel={isExternal || opensWindow ? 'noopener' : undefined}
					attr-download={download}
					{...filterAriaAttributes(configAria)}
					aria-describedby={
						isExternal || opensWindow ? 'linkLabel' : undefined
					}>
					{hasIcon ? _renderHeaderIcon(icon, size) : null}
					{!_isEmpty(link.label) ? _renderHeaderText(link.label) : null}

					{// assist screen readers to announce external links that opens in new tab window
					isExternal || opensWindow ? (
						<span id="linkLabel" hidden>
							{t('Link opens in new window')}
						</span>
					) : null}
				</a>
			);
		};
		const _renderExternalTileLink = (link) => {
			const { opensWindow, configAria, download = false, label } = link;
			const isExternal = isLinkExternal(link.type);

			const headerOverImage =
				tileType === 'block'
					? true
					: tileType === 'image' && tile.content[tileType].headerOverImage;

			let _labelContainerClass = {
				'now-link-set-tile-content': true,
				'-heading': true,
				'-on-top': headerOverImage,
				'-legacy-tile': tileType === 'icon'
			};

			if (tileType !== 'icon') {
				_labelContainerClass = {
					..._labelContainerClass,
					['-' + tile.content[tileType].alignment]: tile.content[tileType]
						.alignment
				};
			}
			return (
				<a
					href={`//${link.value.href}`}
					on-click={() =>
						dispatch(() => {
							return {
								type: 'NOW_LINK_SET#LINK_CLICKED',
								payload: {}
							};
						})
					}
					class={{
						'now-link-set-tile': true,
						'-single-col-grid': headerOverImage,
						'-single-row-grid': tileType === 'icon',
						['-' + variant]: variant,
						['-' + size]: size
					}}
					target={isExternal || opensWindow ? '_blank' : undefined}
					rel={isExternal || opensWindow ? 'noopener' : undefined}
					attr-download={download}
					{...filterAriaAttributes(configAria)}
					aria-describedby={
						isExternal || opensWindow ? 'linkLabel' : undefined
					}>
					{tileType === 'image' || tileType === 'block'
						? _renderImageOrBlock(tile)
						: _renderTileIcon(tile)}

					<div class={_labelContainerClass}>
						<h3
							hook-insert={({ elm }) => _addTooltipEl(elm, label)}
							hook-destroy={(elm) => _removeTooltipEl(elm)}
							class={{
								'now-link-set-tile-content': true,
								'-heading-label': true,
								'-on-top': headerOverImage
							}}>
							{label}
						</h3>
					</div>

					{// assist screen readers to announce external links that opens in new tab window
					isExternal || opensWindow ? (
						<span id="linkLabel" hidden>
							{t('Link opens in new window')}
						</span>
					) : null}
				</a>
			);
		};
		const _renderInternalLink = (link) => {
			const { type = 'external', value = '', configAria } = link;

			const _handleLinkClick = () => {
				if (type === 'route') {
					dispatch(() => {
						return {
							type: 'NAV_ITEM_SELECTED',
							payload: value
						};
					});
				} else if (type === 'modal') {
					dispatch(() => {
						return {
							type: 'MODAL_SELECTED',
							payload: value
						};
					});
				}
				dispatch(() => {
					return {
						type: 'NOW_LINK_SET#LINK_CLICKED',
						payload: {}
					};
				});
			};
			return (
				<button
					type="button"
					on-click={_handleLinkClick}
					class={{
						'now-link-set-heading-link': true,
						['-heading-btn']: true,
						['-one-col-grid']: !hasIcon,
						['-' + placement]: placement,
						['-' + variant]: variant,
						['-' + size]: size
					}}
					title={link.label}
					{...filterAriaAttributes(configAria)}>
					{hasIcon ? _renderHeaderIcon(icon, size) : null}
					{!_isEmpty(link.label) ? _renderHeaderText(link.label) : null}
				</button>
			);
		};
		const _renderInternalTileLink = (link) => {
			const {
				type: linkType = 'external',
				value = '',
				configAria,
				label
			} = link;

			const _handleLinkClick = () => {
				if (linkType === 'route') {
					dispatch(() => {
						return {
							type: 'NAV_ITEM_SELECTED',
							payload: value
						};
					});
				} else if (linkType === 'modal') {
					dispatch(() => {
						return {
							type: 'MODAL_SELECTED',
							payload: value
						};
					});
				}
				dispatch(() => {
					return {
						type: 'NOW_LINK_SET#LINK_CLICKED',
						payload: {}
					};
				});
			};
			const headerOverImage =
				tileType === 'block'
					? true
					: tileType === 'image' && tile.content[tileType].headerOverImage;

			let _labelContainerClass = {
				'now-link-set-tile-content': true,
				'-heading': true,
				'-on-top': headerOverImage,
				'-legacy-tile': tileType === 'icon'
			};

			if (tileType !== 'icon') {
				_labelContainerClass = {
					..._labelContainerClass,
					['-' + tile.content[tileType].alignment]: tile.content[tileType]
						.alignment
				};
			}
			return (
				<button
					type="button"
					on-click={_handleLinkClick}
					class={{
						'now-link-set-tile': true,
						'-single-col-grid': headerOverImage,
						'-single-row-grid': tileType === 'icon',
						['-btn']: true,
						['-' + variant]: variant,
						['-' + size]: size
					}}
					title={link.label}
					{...filterAriaAttributes(configAria)}>
					{tileType === 'image' || tileType === 'block'
						? _renderImageOrBlock(tile)
						: _renderTileIcon(tile)}

					<div class={_labelContainerClass}>
						<h3
							hook-insert={({ elm }) => _addTooltipEl(elm, label)}
							hook-destroy={(elm) => _removeTooltipEl(elm)}
							class={{
								'now-link-set-tile-content': true,
								'-heading-label': true,
								'-legacy-tile': tileType === 'icon',
								'-on-top': headerOverImage
							}}>
							{label}
						</h3>
					</div>
				</button>
			);
		};

		if (isExternal) {
			return hasTile
				? _renderExternalTileLink(link)
				: _renderExternalLink(link);
		} else if (isInternal) {
			return hasTile
				? _renderInternalTileLink(link)
				: _renderInternalLink(link);
		}
	};
	const HeadingAsText = ({ heading, size }) => {
		const { hasIcon, icon, label } = heading;

		const { name, placement, alignment } = icon;

		return (
			<div
				class={{
					'now-link-set-heading-text': true,
					['-one-col-grid']: !hasIcon,
					['-' + placement]: placement,
					['-' + size]: size
				}}>
				{hasIcon ? (
					<span
						class={{
							'now-link-set-heading-text-icon': true,
							['-' + alignment]: alignment
						}}>
						<now-icon icon={name} size={size} />
					</span>
				) : null}

				<h3
					hook-insert={({ elm }) => _addTooltipEl(elm, label)}
					hook-destroy={(elm) => _removeTooltipEl(elm)}
					class={{
						'now-link-set-heading-text-label': true,
						['-' + size]: size
					}}>
					{label}
				</h3>
			</div>
		);
	};

	return (
		<header className="now-link-set-heading">
			{isHeadingText(type) ? (
				<HeadingAsText heading={heading} size={size} />
			) : isHeadingLink(type) ? (
				<HeadingAsLink heading={heading} variant={variant} size={size} />
			) : null}
		</header>
	);
};

const LinksetLinks = ({
	links,
	noIconBullets,
	noHeading,
	indentLinks,
	variant,
	dispatch
}) => {
	const IconBullet = ({ icon }) => {
		return !_isEmpty(icon) ? <now-icon icon={icon} size="md" /> : null;
	};

	const AddIndentation = ({ indent, startsWith, noIconBullets }) => {
		const { icon, align } = !_isEmpty(startsWith) ? startsWith : {};
		return indent && !noIconBullets ? (
			<span
				class={{
					'now-link-set-link-start': true,
					['-' + align]: align
				}}>
				<IconBullet icon={icon} />
			</span>
		) : null;
	};

	const AddIconOrBadge = ({ endsWith: { type, value, additionalAttrs } }) => {
		return (
			<span className="now-link-set-link-end">
				{type === 'icon' ? (
					<now-icon icon="open-link-end-fill" size="md" />
				) : type === 'badge' ? (
					<now-badge value={value} {...additionalAttrs} size="sm" />
				) : null}
			</span>
		);
	};

	const Link = ({ variant, link, noIconBullets, indentLinks }) => {
		const { type } = link;

		const isExternal = isLinkExternal(type);
		const isInternal = isLinkInternal(type);

		const _renderExternalLink = (link) => {
			const {
				label = '',
				value: { href = '' },
				startsWith = {},
				endsWith = {},
				opensWindow,
				configAria,
				selected,
				download = false
			} = link;

			return (
				<a
					href={`//${href}`}
					on-click={() =>
						dispatch(() => {
							return {
								type: 'NOW_LINK_SET#LINK_CLICKED',
								payload: {}
							};
						})
					}
					class={{
						'now-link-set-link': true,
						['-' + variant]: variant,
						['-one-col-grid']: !(indentLinks && !noIconBullets),
						['-is-selected']: selected
					}}
					target={isExternal || opensWindow ? '_blank' : undefined}
					rel={isExternal || opensWindow ? 'noopener' : undefined}
					attr-download={download}
					{...filterAriaAttributes(configAria)}
					aria-describedby={
						isExternal || opensWindow ? 'linkLabel' : undefined
					}>
					{// link with indentation or icon bullets
					!_isEmpty(startsWith) ? (
						<AddIndentation
							indent={indentLinks}
							startsWith={startsWith}
							noIconBullets={noIconBullets}
						/>
					) : null}
					<div
						className="now-link-set-link-text -show-two-lines"
						hook-insert={({ elm }) => _addTooltipEl(elm, label)}
						hook-destroy={(elm) => _removeTooltipEl(elm)}>
						{
							// link label text
							label
						}
						{// links ends with icon or badge
						!_isEmpty(endsWith) ? <AddIconOrBadge endsWith={endsWith} /> : null}
					</div>

					{// assist screen readers to announce external links that opens in new tab window
					isExternal || opensWindow ? (
						<span id="linkLabel" hidden>
							{t('Link opens in new window')}
						</span>
					) : null}
				</a>
			);
		};
		const _renderInternalLink = (link) => {
			const {
				type = '',
				label = '',
				value = {},
				startsWith = {},
				endsWith = {},
				configAria,
				selected
			} = link;

			const _handleLinkClick = () => {
				if (type === 'route') {
					dispatch(() => {
						return {
							type: 'NAV_ITEM_SELECTED',
							payload: value
						};
					});
				} else if (type === 'modal') {
					dispatch(() => {
						return {
							type: 'MODAL_SELECTED',
							payload: value
						};
					});
				}
				dispatch(() => {
					return {
						type: 'NOW_LINK_SET#LINK_CLICKED',
						payload: {}
					};
				});
			};
			return (
				<button
					type="button"
					on-click={_handleLinkClick}
					class={{
						'now-link-set-link': true,
						['-' + variant]: variant,
						['-is-selected']: selected
					}}
					title={link.label}
					{...filterAriaAttributes(configAria)}>
					{// link with indentation or icon bullets
					!_isEmpty(startsWith) ? (
						<AddIndentation
							indent={indentLinks}
							startsWith={startsWith}
							noIconBullets={noIconBullets}
						/>
					) : null}
					<div
						class={{
							'now-link-set-link-text': true,
							['-indent']: indentLinks && _isEmpty(startsWith) && !noIconBullets
						}}>
						{/* link label text */}
						<span
							className="-show-two-lines"
							hook-insert={({ elm }) => _addTooltipEl(elm, label)}
							hook-destroy={(elm) => _removeTooltipEl(elm)}>
							{label}
						</span>

						{// links ends with icon or badge
						!_isEmpty(endsWith) ? <AddIconOrBadge endsWith={endsWith} /> : null}
					</div>
				</button>
			);
		};

		if (isExternal) {
			return _renderExternalLink(link);
		} else if (isInternal) {
			return _renderInternalLink(link);
		}
	};

	const Divider = ({ indentLinks, noIconBullets }) => {
		const includeMargin = indentLinks && !noIconBullets;
		return <li class={{ '-divider': true, '-indent': includeMargin }} />;
	};

	const GroupLinksWithDivider = ({
		variant,
		link,
		indentLinks,
		noIconBullets,
		isFirst,
		isLast
	}) => {
		const groupLinks = getGroupLinks(link);
		let newNoIconBullets =
			noIconBullets ||
			_isEmpty(
				_find(
					groupLinks,
					(l) => !_isEmpty(l.startsWith) && !_isEmpty(l.startsWith.icon)
				)
			);

		return (
			<Fragment>
				{isLast ? (
					<Divider indentLinks={indentLinks} noIconBullets={newNoIconBullets} />
				) : !isFirst && !isLast ? (
					<Divider indentLinks={indentLinks} noIconBullets={newNoIconBullets} />
				) : null}
				<GroupLinks
					variant={variant}
					link={link}
					noIconBullets={noIconBullets}
					indentLinks={indentLinks}
				/>
				{isFirst ? (
					<Divider indentLinks={indentLinks} noIconBullets={newNoIconBullets} />
				) : !isFirst && !isLast ? (
					<Divider indentLinks={indentLinks} noIconBullets={newNoIconBullets} />
				) : null}
			</Fragment>
		);
	};

	const GroupLinks = ({ variant, link, indentLinks, noIconBullets }) => {
		const groupLinks = getGroupLinks(link);
		let newNoIconBullets =
			noIconBullets ||
			_isEmpty(
				_find(
					groupLinks,
					(l) => !_isEmpty(l.startsWith) && !_isEmpty(l.startsWith.icon)
				)
			);

		return groupLinks.map((groupLink) => (
			<li>
				<Link
					link={groupLink}
					variant={variant}
					indentLinks={indentLinks}
					noIconBullets={newNoIconBullets}
				/>
			</li>
		));
	};

	return (
		<ul class={{ '-no-heading': noHeading }}>
			{links.map((link, index) =>
				containsGroup(link) ? (
					<GroupLinksWithDivider
						variant={variant}
						link={link}
						noIconBullets={noIconBullets}
						indentLinks={indentLinks}
						isFirst={index === 0}
						isLast={index === links.length - 1}
					/>
				) : (
					<li>
						<Link
							link={link}
							variant={variant}
							indentLinks={indentLinks}
							noIconBullets={noIconBullets}
						/>
					</li>
				)
			)}
		</ul>
	);
};

const Footer = ({ browseLink, noIconBullets, indentLinks, dispatch }) => {
	const { type, icon, link } = browseLink;

	const isBrowseLinkExternal = isLinkExternal(link.type);
	const isBrowseLinkInternal = isLinkInternal(link.type);
	const _renderBrowseLinkIcon = (icon) => (
		<span className="now-link-set-browse-link-icon">
			<now-icon icon={icon} />
		</span>
	);
	const _renderExternalBrowseLink = (link) => {
		const { opensWindow, configAria, download = false, selected } = link;
		const isExternal = isLinkExternal(link.type);

		return (
			<a
				href={`//${link.value.href}`}
				on-click={() =>
					dispatch(() => {
						return {
							type: 'NOW_LINK_SET#LINK_CLICKED',
							payload: {}
						};
					})
				}
				class={{
					'now-link-set-browse-link': true,
					['-indent']: indentLinks && !noIconBullets,
					['-external']: isBrowseLinkExternal,
					['-is-selected']: selected
				}}
				target={isExternal || opensWindow ? '_blank' : undefined}
				rel={isExternal || opensWindow ? 'noopener' : undefined}
				attr-download={download}
				{...filterAriaAttributes(configAria)}
				aria-describedby={isExternal || opensWindow ? 'linkLabel' : undefined}>
				{!_isEmpty(link.label) ? (
					<div
						className="now-link-set-browse-link-text -show-two-lines"
						hook-insert={({ elm }) => _addTooltipEl(elm, link.label)}
						hook-destroy={(elm) => _removeTooltipEl(elm)}>
						{link.label}
						{!_isEmpty(icon) ? _renderBrowseLinkIcon(icon) : null}
					</div>
				) : null}

				{// assist screen readers to announce external links that opens in new tab window
				isExternal || opensWindow ? (
					<span id="linkLabel" hidden>
						{t('Link opens in new window')}
					</span>
				) : null}
			</a>
		);
	};
	const _renderInternalBrowseLink = (link, icon) => {
		const { type = 'external', value = '', configAria, selected } = link;

		const _handleLinkClick = () => {
			if (type === 'route') {
				dispatch(() => {
					return {
						type: 'NAV_ITEM_SELECTED',
						payload: value
					};
				});
			} else if (type === 'modal') {
				dispatch(() => {
					return {
						type: 'MODAL_SELECTED',
						payload: value
					};
				});
			}
			dispatch(() => {
				return {
					type: 'NOW_LINK_SET#LINK_CLICKED',
					payload: {}
				};
			});
		};
		return (
			<button
				type="button"
				on-click={_handleLinkClick}
				class={{
					'now-link-set-browse-link': true,
					['-indent']: indentLinks && !noIconBullets,
					['-internal']: isBrowseLinkInternal,
					['-is-selected']: selected
				}}
				title={link.label}
				{...filterAriaAttributes(configAria)}>
				{!_isEmpty(link.label) ? (
					<div
						className="now-link-set-browse-link-text -show-two-lines"
						hook-insert={({ elm }) => _addTooltipEl(elm, link.label)}
						hook-destroy={(elm) => _removeTooltipEl(elm)}>
						{link.label}
						{!_isEmpty(icon) ? _renderBrowseLinkIcon(icon) : null}
					</div>
				) : null}
			</button>
		);
	};

	return (
		<footer>
			{type === 'link' && isBrowseLinkInternal
				? _renderInternalBrowseLink(link, icon)
				: type === 'link' && isBrowseLinkExternal
				? _renderExternalBrowseLink(link)
				: null}
		</footer>
	);
};

const nowLinkSet = (state, { dispatch }) => {
	// eslint-disable-next-line no-unused-vars
	const {
		minWidth,
		width,
		heading,
		variant,
		size,
		indentLinks,
		links,
		browseLink
	} = state.properties;

	const noIconBullets = _isEmpty(
		_find(links, (l) => !_isEmpty(l.startsWith) && !_isEmpty(l.startsWith.icon))
	);

	const noHeading = _isEmpty(heading);

	const getNavLabel = (heading) => {
		const { type, label, link = {} } = heading;
		if (type === 'text') {
			return label;
		} else if (type === 'link') {
			return link.label;
		}

		return 'linkset';
	};

	const navLabel = getNavLabel(heading);

	return (
		<nav
			className="now-link-set"
			style={{
				minWidth: `${minWidth}px`,
				width: `${width}px`
			}}
			aria-label={navLabel}>
			<Header
				heading={heading}
				variant={variant}
				size={size}
				dispatch={dispatch}
			/>
			<LinksetLinks
				links={links}
				noIconBullets={noIconBullets}
				noHeading={noHeading}
				indentLinks={indentLinks}
				variant={variant}
				dispatch={dispatch}
			/>
			{!_isEmpty(browseLink) ? (
				<Footer
					browseLink={browseLink}
					noIconBullets={noIconBullets}
					indentLinks={indentLinks}
					dispatch={dispatch}
				/>
			) : null}
		</nav>
	);
};

/**
 * A group of links that provide an interactive reference to a resource. The target resource
 * can be either within or external to the current page or application. External links
 * open in a new browser tab.
 *
 *
 *
 * ```jsx
 *  const heading = {
 *    type: 'text',
 *    hasIcon: true,
 *    icon: {
 *    	name: 'calendar-clock-outline',
 *    	placement: 'inline',
 *			alignment: 'left'
 * 		},
 *		label: 'Time off',
 *    link: {}
 *  };
 *  const links = [
 *		label: '2019 Holiday Calendar (U.S.)',
 *		value: {
 *			href: 'www.servicenow.com'
 * 		},
 * 		type: 'external',
 * 		startsWith: { icon: 'document-outline', align: 'left' },
 * 		endsWith: {
 * 			type: 'icon',
 * 			value: 'clipboard-outline',
 * 			additionalAttrs: null
 * 		}
 * 	];
 *  const browseLink = {
 *  	type: 'route'
 *		label: '2019 Holiday Calendar (U.S.)',
 *		value: {
 *			route: 'browse',
 *			fields: {}
 * 		},
 * 		opensWindow: false,
 *  };
 *
 *  <now-link-set heading={heading} links={links} browseLink={browseLink} size="lg" variant="secondary"/>
 *
 * ```
 *
 * @seismicElement now-link-set
 * @summary A group of subcomponents that provides a structured set of links 
 * and other useful elements that enable users to navigate to different resources. 
 */
createCustomElement('now-link-set', {
	properties: {
		/**
		 * Sets the minimum width in pixels.
		 * @type {(150)}}
		 */
		minWidth: {
			default: 150,
			schema: { type: 'number' }
		},
		/**
		 * Sets the custom width in pixels.
		 * @type {(150)}}
		 */
		width: {
			default: 300,
			schema: { type: 'number' }
		},

		/**
		 * Defines the font size.
		 * @type {('md'|'lg')}}
		 */
		size: { default: 'lg', schema: { type: 'string', enum: ['md', 'lg'] } },
		/**
		 * Sets plain and link text colors.
		 * @type {('default'|'primary'|'secondary')}
		 */
		variant: {
			default: 'default',
			schema: { type: 'string', enum: ['default', 'primary', 'secondary'] }
		},
		/**
		 * Text that provides a short summary of content in the link set.
		 * The heading may be a link or plain text. You can optionally include an icon
		 * in one of the following positions:
		 *  - Above the heading.
		 *  - To the left of the heading.
		 *
		 * @type {(Object.<Heading>)}
		 */
		heading: {
			default: {},
			schema: headingSchema
		},
		/**
		 * When true, indents the links.
		 * @type {boolean}
		 */
		indentLinks: { default: true, schema: { type: 'boolean' } },
		/**
		 * A group of links that provide an interactive reference to a resource.
		 * The resources can be either within or external to the current page or application. 
 		 * External links open in a new browser tab.
		 *
		 * All internal links either dispatch NAV_ITEM_SELECTED or MODAL_SELECTED actions. See more about actions below.
		 * @type {(Object.<Links>)}
		 */
		links: { default: [], schema: LinksSchema },
		/**
		 * Optionally configures a Browse link at the bottom of the link set to allow
		 * the user to open an expanded view of the link set. 
		 *
		 * All internal links either dispatch NAV_ITEM_SELECTED or MODAL_SELECTED actions. See more about actions below.
		 * @type {(Object.<BrowseLink>)}
		 */
		browseLink: {
			default: {},
			schema: browseLinkSchema
		}
	},
	dispatches: {
		/**
		 * Dispatched when a link points to an internal application page.
		 * @type {{route: 'browse', fields: {}}}
		 */
		NAV_ITEM_SELECTED: {},
		/**
		 * Dispatched when a link opens a modal.
		 * @type {{modalId: '#my-modal', showModal: true, displayOptions: {}}}
		 */
		MODAL_SELECTED: {},
		/**
		 * Dispatched when the user clicks any link.
		 * @type {{}}
		 */
		'NOW_LINK_SET#LINK_CLICKED': {}
	},
	renderer: {
		type: snabbdom,
		view: nowLinkSet
	},
	styles: nowLinkSetStyles,
	behaviors: [rtlBehavior]
});
