import { has as _has, get as _get, isEmpty as _isEmpty } from 'lodash';

export const containsGroup = (link) =>
	_has(link, 'group') &&
	!_isEmpty(_get(link, 'group')) &&
	Array.isArray(_get(link, 'group'));

export const getGroupLinks = (link) =>
	containsGroup(link) ? _get(link, 'group') : null;

export const isLinkExternal = (linkType) => linkType === 'external';

export const isLinkInternal = (linkType) =>
	linkType === 'route' || linkType === 'modal';

export const isHeadingText = (headingType) => headingType === 'text';

export const isHeadingLink = (headingType) => headingType === 'link';

export const setTargetRef = (ref, updateProperties) => {
	const spanIconMarginTop = 2;
	const showTooltip = ref.clientHeight + spanIconMarginTop < ref.scrollHeight;

	if (showTooltip) {
		updateProperties({ targetRef: ref.parentNode });
	}
};

export const hookInsertFn = (updateProperties) => ({ elm }) =>
	setTargetRef(elm, updateProperties);

export const hookDestoryFn = (updateProperties) => () =>
	setTargetRef(null, updateProperties);
